import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip';
import { Info, Minus, Plus } from 'lucide-react';

interface DependentsInputProps {
  value: number;
  onChange: (value: number) => void;
  min?: number;
  max?: number;
}

export function DependentsInput({ value, onChange, min = 0, max = 20 }: DependentsInputProps) {
  const handleIncrement = () => {
    if (value < max) {
      onChange(value + 1);
    }
  };

  const handleDecrement = () => {
    if (value > min) {
      onChange(value - 1);
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = parseInt(e.target.value, 10);
    if (!isNaN(newValue) && newValue >= min && newValue <= max) {
      onChange(newValue);
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    // Enter key: blur input
    if (e.key === 'Enter') {
      e.currentTarget.blur();
      return;
    }

    // Escape key: reset to 0
    if (e.key === 'Escape') {
      onChange(0);
      return;
    }

    // Arrow keys: increment/decrement by 1
    if (e.key === 'ArrowUp') {
      e.preventDefault();
      handleIncrement();
    } else if (e.key === 'ArrowDown') {
      e.preventDefault();
      handleDecrement();
    }
  };

  return (
    <div className="space-y-2">
      <div className="flex items-center gap-2">
        <Label htmlFor="dependents">S ngi ph thuc</Label>
        <TooltipProvider>
          <Tooltip>
            <TooltipTrigger asChild>
              <Button variant="ghost" size="icon" className="h-4 w-4 p-0">
                <Info className="h-3 w-3" />
                <span className="sr-only">Thng tin ngi ph thuc</span>
              </Button>
            </TooltipTrigger>
            <TooltipContent className="max-w-xs">
              <p className="text-sm">
                Ngi ph thuc l con di 18 tui, v/chng khng c thu nhp, cha m trn 60 tui
                khng c thu nhp, hoc ngi khuyt tt khng c kh nng lao ng.
              </p>
            </TooltipContent>
          </Tooltip>
        </TooltipProvider>
      </div>
      <div className="flex items-center gap-2">
        <Button
          type="button"
          variant="outline"
          size="icon"
          onClick={handleDecrement}
          disabled={value <= min}
          aria-label="Gim s ngi ph thuc"
        >
          <Minus className="h-4 w-4" />
        </Button>
        <Input
          id="dependents"
          type="number"
          min={min}
          max={max}
          value={value}
          onChange={handleInputChange}
          onKeyDown={handleKeyDown}
          className="text-center"
          aria-label="S ngi ph thuc. Dng mi tn ln/xung hoc nt +/-, Enter  xc nhn, Escape  t v 0"
        />
        <Button
          type="button"
          variant="outline"
          size="icon"
          onClick={handleIncrement}
          disabled={value >= max}
          aria-label="Tng s ngi ph thuc"
        >
          <Plus className="h-4 w-4" />
        </Button>
      </div>
      <p className="text-sm text-muted-foreground">
        T {min} n {max} ngi
      </p>
    </div>
  );
}
